import React from 'react'
import {Redirect, Route, Switch} from 'react-router-dom'
import {Error404} from '../pages/Error404'
import {Login} from '../pages/Login'
import {CheckIn} from '../pages/CheckIn';
import {Profile} from '../pages/Profile';
import {EnteringNewPassword} from '../pages/EnteringNewPassword';
import {PasswordRecovery} from '../pages/PasswordRecovery';


export const Routes = () => {
    return (
        <div>
            {/*Switch –≤—ã–±–∏—Ä–∞–µ—Ç –ø–µ—Ä–≤—ã–π –ø–æ–¥—Ö–æ–¥—è—â–∏–π —Ä–æ—É—Ç*/}
            <Switch>

                {/*–≤ –Ω–∞—á–∞–ª–µ –º—ã –ø–æ–ø–∞–¥–∞–µ–º –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É '/' –∏ –ø–µ—Ä–µ—Ö–æ–¥–∏–º —Å—Ä–∞–∑—É –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É PRE_JUNIOR*/}
                {/*exact –Ω—É–∂–µ–Ω —á—Ç–æ–± —É–∫–∞–∑–∞—Ç—å –ø–æ–ª–Ω–æ–µ —Å–æ–≤–ø–æ–¥–µ–Ω–∏–µ (—á—Ç–æ –ø–æ—Å–ª–µ '/' –Ω–∏—á–µ–≥–æ –Ω–µ –±—É–¥–µ—Ç)*/}
                <Route path={'/'} exact render={() => <Redirect to={'/'}/>}/>

                <Route path={'/login'} render={() => <Login/>}/>
                <Route path={'/checkIn'} render={() => <CheckIn/>}/>
                <Route path={'/profile'} render={() => <Profile/>}/>
                <Route path={'/passwordRecovery'} render={() => <PasswordRecovery/>}/>
                <Route path={'/enteringNewPassword'} render={() => <EnteringNewPassword/>}/>

                {/*—É —ç—Ç–æ–≥–æ —Ä–æ—É—Ç–∞ –Ω–µ—Ç –ø—É—Ç–∏, –æ–Ω –æ—Ç—Ä–∏—Å—É–µ—Ç—Å—è –µ—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∑–∞—Ö–æ—á–µ—Ç –ø–æ–ø–∞—Å—Ç—å –Ω–∞ –Ω–µ—Å—É—â–µ—Å—Ç–≤—É—é—â—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É*/}
                <Route path={'/404'} render={() => <Error404/>}/>
                <Redirect from={'*'} to={'/404'}/>

            </Switch>
        </div>
    )
}